/*.navbar styles: default, static, fixed*/
.navbar-default {
  width: 100%;
  position: relative;
  height: 10px; }

.navbar-static {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0; }

.navbar-fixed {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000; }

/*general navbar specs*/
.navbar-header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 72px; }

.navbar-brand {
  font-size: 1.8em;
  padding: 0px;
  padding-left: 28px; }

.navbar-menu {
  height: 72px;
  overflow: hidden;
  transition: height .6s ease;
  width: 100%; }

.navbar-navigation {
  display: flex;
  flex-direction: column;
  list-style: none;
  padding-left: 0;
  margin: 0px; }

.navbar-menu a,
.navbar-item a {
  text-decoration: none;
  color: inherit;
  cursor: pointer;
  transition: all .2s ease-in-out;
  font-weight: 600;
  color: inherit; }

.navbar-menu a:hover,
.navbar-item a:hover {
  text-decoration: none;
  color: #70F8BA;
  cursor: pointer;
  transform: scale(1.1); }

.navbar-item a {
  padding: 23px;
  display: block;
  letter-spacing: 2px; }

/*navbar-menu-transparent*/
.navbar-menu-transparent {
  color: #4A4E69;
  background-color: transparent;
  border-bottom: 1px solid #2A2D34; }

.navbar-menu-transparent .navbar-item:nth-child(2) {
  border-top: 1px solid #2A2D34; }

.navbar-menu-transparent .navbar-hamburger span::after,
.navbar-menu-transparent .navbar-hamburger span::before,
.navbar-menu-transparent .navbar-hamburger span {
  background-color: #ecf0f1; }

/* navbar-menu-alt */
.navbar-menu-alt {
  color: #fff;
  background-color: #4A4E69; }

.navbar-menu-alt .navbar-hamburger span::after,
.navbar-menu-alt .navbar-hamburger span::before,
.navbar-menu-alt .navbar-hamburger span {
  background-color: #ecf0f1; }

/* navbar-menu-primary */
.navbar-menu-prime {
  color: #fff;
  background-color: #70F8BA; }

.navbar-menu-prime .navbar-hamburger span::after,
.navbar-menu-prime .navbar-hamburger span::before,
.navbar-menu-prime .navbar-hamburger span {
  background-color: #ecf0f1; }

/* navbar-menu-secondary */
.navbar-menu-secondary {
  color: #fff;
  background-color: #6D8A96; }

.navbar-menu-secondary .navbar-hamburger span::after,
.navbar-menu-secondary .navbar-hamburger span::before,
.navbar-menu-secondary .navbar-hamburger span {
  background-color: #ecf0f1; }

/*general hamburger specs*/
.navbar-hamburger {
  padding: 26px;
  position: relative; }

.navbar-hamburger span::before {
  margin-top: -8px; }

.navbar-hamburger span::after {
  margin-top: 8px; }

.navbar-hamburger span::after,
.navbar-hamburger span::before {
  content: '';
  position: absolute; }

.navbar-hamburger span::after,
.navbar-hamburger span::before,
.navbar-hamburger span {
  display: block;
  height: 2px;
  width: 25px;
  transition: .5s ease-out; }

#navbar-checkbox {
  display: none; }

#navbar-checkbox:checked ~ .navbar-menu {
  height: 100vh;
  overflow: auto;
  background: rgba(0, 0, 0, 0.5);
  transition: .5s ease-out; }

#navbar-checkbox:checked ~ .navbar-menu li .navbar-hamburger span {
  background-color: transparent; }

#navbar-checkbox:checked ~ .navbar-menu li .navbar-hamburger span::before,
#navbar-checkbox:checked ~ .navbar-menu li .navbar-hamburger span::after {
  margin-top: 0px; }

/*hamburger-themes: spin, doublespin*/
/*hamburger-spin*/
#navbar-checkbox:checked ~ .navbar-menu li .navbar-hamburger-spin span::before {
  transform: rotate(45deg); }

#navbar-checkbox:checked ~ .navbar-menu li .navbar-hamburger-spin span::after {
  transform: rotate(-45deg); }

/*hamburger-spin*/
#navbar-checkbox:checked ~ .navbar-menu li .navbar-hamburger-doublespin span::before {
  transform: rotate(225deg); }

#navbar-checkbox:checked ~ .navbar-menu li .navbar-hamburger-doublespin span::after {
  transform: rotate(-225deg); }

/*behavior under screenwidth 768*/
@media screen and (min-width: 768px) {
  .navbar-navigation {
    flex-flow: row;
    justify-content: flex-end; }
  .navbar-header {
    margin-right: auto; }
  .navbar-hamburger {
    display: none; }
  .navbar-menu .navbar-item:nth-child(2) {
    border-top: 0px; } }

/* Buttons
  =============== */
.button, a.button, button, [type=submit], [type=reset], [type=button] {
  display: inline-block;
  line-height: 1;
  border: 1px solid #70F8BA;
  border-radius: 5px;
  background: #70F8BA;
  color: #fff;
  font-weight: 500;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 1rem;
  text-transform: normal;
  padding: .7rem 1.2rem;
  margin: 0;
  white-space: nowrap;
  vertical-align: middle;
  text-align: center;
  cursor: pointer;
  text-decoration: none; }

.button:hover, a.button:hover, button:hover, [type=submit]:hover, [type=reset]:hover, [type=button]:hover {
  border: 1px solid #3ff6a3;
  background: #0bde7e;
  color: #fff;
  text-decoration: none; }

.button:focus, .button:active, a.button:focus, a.button:active, button:focus, button:active, [type=submit]:focus, [type=submit]:active, [type=reset]:focus, [type=reset]:active, [type=button]:focus, [type=button]:active {
  border: 1px solid #3ff6a3;
  background: #3ff6a3;
  color: #fff;
  text-decoration: none; }

.button::-moz-focus-inner, a.button::-moz-focus-inner, button::-moz-focus-inner, [type=submit]::-moz-focus-inner, [type=reset]::-moz-focus-inner, [type=button]::-moz-focus-inner {
  border: 0;
  padding: 0; }

.secondary-button,
a.secondary-button {
  color: #70F8BA;
  border: 1px solid transparent;
  background: transparent; }
  .secondary-button:hover, .secondary-button:focus, .secondary-button:active,
  a.secondary-button:hover,
  a.secondary-button:focus,
  a.secondary-button:active {
    color: #70F8BA;
    border: 1px solid transparent;
    background: transparent; }

.round-button,
a.round-button {
  border-radius: 40px; }

